<template lang="pug">
  #userCenter.mt-6.pt-3
    b-container
      h2.text-white.h2.my-5 
      b-tabs(content-class='bg-light rounded-lg' card)
        b-tab(title='' active)
          b-container
            h2.my-4 
            div.my-4
              h5.ml-5.d-inline :
              h5.ml-3.d-inline(v-if="!nameEdit") {{ user.name }}
              b-form-input.d-inline.ml-3(v-if="nameEdit" v-model="user.model.userName")
              b-button.ml-4.mb-1(size="sm" variant="info" v-if="!nameEdit" @click="edit('nameEdit')") 
              b-button.ml-4.mb-1(size="sm" variant="success" v-if="nameEdit" @click="save('nameEdit', user.model)") 
              b-button.ml-2.mb-1(size="sm" v-if="nameEdit" @click="cancel('nameEdit','nameModel','userName')") 
            div.my-4
              h5.ml-5.d-inline :
              h5.ml-3.d-inline(v-if="!accountEdit") {{ user.account }}
              b-form-input.d-inline.ml-3(v-if="accountEdit" v-model="user.model.account")
              b-button.ml-4.mb-1(size="sm" variant="info" v-if="!accountEdit" @click="edit('accountEdit')") 
              b-button.ml-4.mb-1(size="sm" variant="success" v-if="accountEdit" @click="save('accountEdit', user.model)") 
              b-button.ml-2.mb-1(size="sm" v-if="accountEdit" @click="cancel('accountEdit','accountModel','account')") 
            div.my-4
              h5.d-inline.ml-5 :
              h5.d-inline.ml-3 {{ user.userGender }}
            div.my-4
              h5.d-inline.ml-5 :
              h5.d-inline.ml-3 {{ user.id }}
        b-tab(title='').mb-5
          b-container
            h2.my-3 
            b-row
              b-col.mx-auto.my-3(cols="10" v-for="(ticket, index) in tickets" :key="ticket._id")
                h3 {{ ticket.title }}
                p {{ ticket.time }}
                p {{ ticket.location }}
                p {{ ticket.quantity }}
                p(v-if="ticket.paid") 
                p(v-if="!ticket.paid") 
                b-button(variant="info") 
                b-button(variant="success" v-b-modal.approach @click="approach(ticket.cid)").ml-2 
      b-modal#approach
        template(#modal-header='{ close }')
          h3 
          b-button(variant="light" @click="close()") 
            font-awesome-icon(:icon="['far', 'times-circle']")
        template(#default='{ hide }')
          b-row
            b-col.d-flex.justify-content-center
              vue-qrcode(:value="value")
        template(#modal-footer='{ ok, cancel, hide }')
          h6.mr-auto &nbsp;
</template>

<script>
import VueQrcode from 'vue-qrcode'
export default {
  name: 'UserCenter',
  data() {
    return {
      user: {},
      nameEdit: false,
      accountEdit: false,
      tickets: {},
      value: {}
    }
  },
  components: {
    VueQrcode
  },
  methods: {
    edit(edit) {
      this[edit] = true
    },
    cancel(edit, model, data) {
      this.user[model] = this.user[data]
      this[edit] = false
    },
    save(edit, data) {
      this.axios.patch(process.env.VUE_APP_API + '/users/edit', data)
        .then(res => {
          if (res.data.success) {
            this[edit] = false
            this.user.name = data.userName
            this.user.account = data.account
          } else {
            this.$swal({
              icon: 'error',
              title: '',
              text: res.data.message
            })
          }
        })
        .catch(err => {
          this.$swal({
            icon: 'error',
            title: '',
            text: err.response.data.message
          })
        })
    },
    approach(id) {
      this.value = id
    }
  },
  mounted() {
    this.axios.get(process.env.VUE_APP_API + '/users/user')
      .then(res => {
        if (res.data.success) {
          this.user = {
            id: res.data.result._id,
            account: res.data.result.account,
            name: res.data.result.userName,
            model: {
              userName: res.data.result.userName,
              account: res.data.result.account
            },
            gender:res.data.result.gender
          }
          this.tickets = res.data.result.tickets
          this.tickets.map(ticket => {
            ticket.tid = ticket._id
            ticket.title = ticket.t_id.title
            ticket.time = ticket.t_id.time
            ticket.location = ticket.t_id.location
            ticket.quantity = ticket.quantity
            ticket.cid = ticket.t_id._id
            delete ticket.t_id
          })
          if (this.user.gender === 'male') {
            this.user.userGender = ''
          } else if (this.user.gender === 'female') {
            this.user.userGender = ''
          }
        }
      })
  }
}
</script>

<style>
#userCenter li a{
  color: white;
}
#userCenter li .active {
  color: black;
}
#userCenter input {
  width: 200px;
}
.col img {
  width: 300px;
}
.modal-header,
.modal-footer {
  border: none;
}
</style>
